* {
    margin: 0;
    list-style: none;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
}

body {
    background: -webkit-linear-gradient(top, #2C3F57, #160B16);
    background-repeat: no-repeat;
    -webkit-overflow-scrolling: touch;
    background-attachment: fixed;
    height: 100%;
    width: 100%;
    margin: 0;
}

.modal {
    position: fixed;
    z-index: 400;
    top: 200px;
    display: block;
    color: white;
    background: #2C3F57;
    border-radius: 15px;
}
.modal-content {
    display: grid;
    grid-template-columns: 20% 80%;
    grid-gap: 1rem;
}
.account-details {
    font-size: 1.2em;
    color: #fff;
}
.account-d {
    font-size: 1.5em;
    color: #6AF2FF;
    font-weight: 500;
    margin-bottom: 30px;
}
.modal-off {
    display: none;
}

.nav_wrapper {
    margin: 0;
}

.nav_wrapper .top_navbar {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    position: fixed;
    z-index: 90;
}

.nav_wrapper .top_navbar .hamburger {
    width: 70px;
    height: 100%;
    background: #2A628F;
    padding: 15px 17px;
    cursor: pointer;
}
.nav_wrapper .top_navbar .hamburger div {
    width: 30px;
    height: 4px;
    background: #fff;
    margin: 5px 0;
    border-radius: 7px;
}

.tinydiv {
    width: 20px !important;
}

.nav_wrapper .top_navbar .top_menu {
    width: calc(100% - 70px);
    background: rgb(40, 56, 77);
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px 0 0;
    box-shadow: 0 3px 3px rgba(0,0,0,0.1);
}

.logo {
    margin: 0;
}
.logo_image {
    width: 75px;
}

.nav_wrapper .sidebar{
    position: fixed;
    top: 60px;
    background: #2A628F;
    width: 200px;
    height: calc(100vh - 80px);
    border-bottom-left-radius: 30px;
    transition: all 0.3s ease;
    z-index: 100;
}
  
.nav_wrapper .sidebar ul li a{
    display: block;
    padding: 20px;
    color: #fff;
    position: relative;
    margin-bottom: 1px;
    white-space: nowrap;
}

.nav_wrapper .sidebar ul li a:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #92a6e2;
    display: none;
}

.nav_wrapper .sidebar ul li a span.icon{
    margin-right: 10px;
    display: inline-block;
}

.nav_wrapper .sidebar ul li a span.title {
    display: inline-block;
}

.nav_wrapper .sidebar ul li a:hover,
.nav_wrapper .sidebar ul li a.active {
    background: #215985;
    color: #E6E6E6;
}

.nav_wrapper .sidebar ul li a:hover:before,
.nav_wrapper .sidebar ul li a.active:before{
    display: block;
}

.collapse .sidebar {
    width: 70px;
}

.coloz {
    position: fixed;
    top: 60px;
    background: #2A628F;
    width: 70px;
    height: calc(100vh - 80px);
    border-bottom-left-radius: 30px;
    transition: all 0.3s ease;
    z-index: 100;
}

.collapse .sidebar ul li a {
    text-align: center;
}

.coloz ul li a {
    text-align: center;
    display: block;
    padding: 20px;
    color: #fff;
    position: relative;
    margin-bottom: 1px;
    white-space: nowrap;
}
.collapse .sidebar ul li a span.icon {
    margin: 0;
}
.coloz ul li a span.icon {
    margin: 0;
    display: inline-block;
}

.coloz ul li a span.title {
    display: none;
}
coloz ul li a:hover,
.coloz ul li a.active {
    background: #215985;
    color: #E6E6E6;
}

.coloz ul li a:hover:before,
.coloz ul li a.active:before{
    display: block;
}
.collapse .sidebar ul li a span.title {
    display: none;
}
