.animation-area {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 130vh;
    overflow: hidden;
    z-index: -100;
}
.animation-area li {
    position: absolute;
    display: block;
    list-style: none;
    width: 25px;
    height: 25px;
    border: 5px solid rgba(233, 233, 233, 0.1);
    -webkit-animation: animate 20s linear infinite;
            animation: animate 20s linear infinite;
    bottom: -150px;
}
.animation-area li:nth-child(1) {
    left: 86%;
    width: 80px;
    height:80px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}
.animation-area li:nth-child(2) {
    left: 12%;
    width: 30px;
    height:30px;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
}
.animation-area li:nth-child(3) {
    left: 70%;
    width: 100px;
    height:100px;
    -webkit-animation-delay: 5.5s;
            animation-delay: 5.5s;
}
.animation-area li:nth-child(4) {
    left: 42%;
    width: 150px;
    height:150px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 15s;
            animation-duration: 15s;
}
.animation-area li:nth-child(5) {
    left: 65%;
    width: 40px;
    height:40px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}
.animation-area li:nth-child(6) {
    left: 15%;
    width: 110px;
    height: 110px;
    -webkit-animation-delay: 3.5s;
            animation-delay: 3.5s;
}
@-webkit-keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
    }
    100% {
        transform: translateY(-800px) rotate(360deg);
        opacity: 0;
    }
}
@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
    }
    100% {
        transform: translateY(-800px) rotate(360deg);
        opacity: 0;
    }
}
